import React from 'react';
import Item from '../../Components/Item'
import './index.scss';

function Home({ items }){
    const completedItems = items.filter(item => item.completed).reverse();
    const pendingItems = items.filter(item => !item.completed).reverse();

    return (
        <div className="page-home">
            <div className="card page-home-pending">
                <h1 className="card-title">
                    <span>Pending tasks</span>
                    <span>{ pendingItems.length } items</span>
                </h1>
                {
                    pendingItems.map(item => (
                        <Item
                            key={ item.id }
                            item={ item }
                        />
                    ))
                }
                {
                    pendingItems.length === 0 ? (
                        <div className="empty-list">
                            <i className="fa fa-meh"/>
                            <span>list is empty</span>
                        </div>
                    ) : null
                }
            </div>
            <div className="card page-home-completed">
                <h1 className="card-title">
                    <span>Completed tasks</span>
                    <span>{ completedItems.length } items</span>
                </h1>
                {
                    completedItems.map(item => (
                        <Item
                            key={ item.id }
                            item={ item }
                        />
                    ))
                }
                {
                    completedItems.length === 0 ? (
                        <div className="empty-list">
                            <i className="fa fa-meh"/>
                            <span>list is empty</span>
                        </div>
                    ) : null
                }
            </div>
        </div>
    )
}

export default Home;