import { v4 } from 'node-uuid';
import * as ActionTypes from '../ActionTypes';

export function addItem(title, url = null) {
    return {
        type : ActionTypes.ITEM_ADD,
        title,
        url,
        completed : false,
        id : v4()
    }
}
export function removeItem(id) {
    return {
        type : ActionTypes.ITEM_REMOVE,
        id
    }
}
export function setItemStatus(id, completed = false) {
    return {
        type : ActionTypes.ITEM_SET_STATUS,
        id,
        completed
    }
}

export function setItemsFilter(filter = 'all') {
    return {
        type : ActionTypes.ITEMS_SET_FILTER,
        filter
    };
}

export function setPage(page) {
    return {
        type : ActionTypes.PAGE_SET,
        page
    }
}

export function setState(state) {
    return {
        type : ActionTypes.STATE_SET,
        state
    }
}