import { createStore } from "redux";
import RootReducer from './Reducers';
import { throttle } from 'lodash';
import StateStorage from '../Common/StateStorage';
import { setState } from "./Actions";
import initialState from './InitialState';

const store = createStore(RootReducer, Object.assign(
    {},
    initialState,
    StateStorage.get()
));

window.onstorage = ({ key, newValue }) => {
    if(key === 'state'){
        store.dispatch(setState(JSON.parse(newValue)));
    }
};

// on state change => save state in localStorage
store.subscribe(throttle(() => {
    StateStorage.set(store.getState());
}));

export default store;