import React, { useState } from 'react';
import Input from '../../Components/Input';
import './index.scss';

function Add({ addItem }) {
    const [title, setTitle] = useState('');
    const onTitleChange = ({ target }) => setTitle(target.value);

    const [url, setUrl] = useState('');
    const onUrlChange = ({ target }) => setUrl(target.value);

    const onAdd = () => {
        if(title.trim() === '') return;

        addItem(title.trim(), url.trim());
        setTitle('');
        setUrl('');
    };

    return (
        <div className="page-add">
            <div className="card page-add-container">
                <h1 className="card-title">Add new task</h1>
                <Input
                    placeholder="task title"
                    value={ title }
                    onChange={ onTitleChange }
                />
                <br/>
                <Input
                    placeholder="task url (optional)"
                    value={ url }
                    onChange={ onUrlChange }
                />
                <br/>
                <button
                    onClick={ onAdd }
                    className="btn btn-primary"
                >Add</button>
            </div>
        </div>
    );
}

export default Add;