import React from 'react';

const menuItems = [
    {
        title : 'home',
        key : 'home',
        icon : 'home'
    },{
        title : 'add',
        key : 'add',
        icon : 'plus'
    },{
        title : 'info',
        key : 'info',
        icon : 'info'
    }
];

function Navigation({ page, setPage }){
    return (
        <ul className="navigation-container">
            { menuItems.map(({key, icon, title}) => (
                <li
                    key={ key }
                    className={key === page ? 'active' : ''}
                    onClick={ () => setPage(key) }
                >
                    <i className={'fa fa-' + icon}/>
                    <span>{ title }</span>
                </li>
            )) }
        </ul>
    );
}

export default Navigation;