import React from 'react';
import './index.scss';

function Item({ item, removeItem, setItemStatus}){
    const {url, title, id, completed} = item;

    const onDelete = () => {
          removeItem(id);
    };
    const onComplete = () => {
        setItemStatus(id, !completed);
    };

    return (
        <div className="todo-item">
            {
                url === undefined || url === '' ? (
                    <span>{ title }</span>
                ) : (
                    <a href={ url }>{ title }</a>
                )
            }
            <div
                className="todo-item-options"
            >
                <span
                    className="todo-item-delete"
                    onClick={ onDelete }
                ><i className="fa fa-times"/></span>
                <span
                    onClick={ onComplete }
                    className="todo-item-complete"
                ><i className={'fa ' + (!completed ? 'fa-check' : 'fa-check-double')}/></span>
            </div>
        </div>
    )
}
export default Item;